<template>
    <b-container class="bv-example-row">
        <b-row align-h="center" v-for="(item, index) in items" :key="index">
            <b-card 
                style="max-width: 20rem;"
                class="mb-2"   
            >
                <b-button :href="item.link" variant="dark" size="lg">{{item.title}}</b-button>
            </b-card>
        </b-row>
    </b-container>
</template>
<script>
import {ultraSecretUberHiddenKey} from '../contants'
import Cryptr from 'cryptr'
const cryptr = new Cryptr(ultraSecretUberHiddenKey);

export default {
    mounted() {
        let route = window.location.pathname.replace('/', '');
        let itemsString = cryptr.decrypt(route);
        let items = JSON.parse(itemsString);
        let self = this;
        items.forEach(item => {
            self.items.push(item)
        });
    },
    data: () => {
        return {
            items: []
        }
    }
}
</script>