<template>
    <b-container>
        <h1 style="text-align:center">Link Multiplier</h1>
        <b-row align-h="center">
            <b-button @click="() => showHelp()" variant="link">What is this?</b-button>
        </b-row>
        <b-form @submit="onSubmit">
        <b-form-group
            id="input-group-1"
            label="Title"
            label-for="input-1"
            description="This will be the text displayed on the button"
        >
            <b-form-input
                id="input-1"
                v-model="linkText"
                required
                placeholder="Enter Title: (NOTE: 40 characters or less)"
                maxlength="40"
            ></b-form-input>
        </b-form-group>

        <b-form-group 
            id="input-group-2" 
            label="URL" 
            label-for="input-2"
            description="This is the link that the user is taken to when they click on the button"
        >
            <b-form-input
            id="input-2"
            v-model="linkLink"
            required
            type="url"
            placeholder="Enter URL: (eg. http://www.google.com)"
            ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Add Item</b-button>
        </b-form>
        <b-card class="mt-3">
            <b-container slot="header">
                <b-input-group class="mt-3">
                    <b-button :disabled="links.length <= 0" @click="generateLink">Generate</b-button>
                    <b-form-input id="generatedOuput" v-model="generated"></b-form-input>
                    <b-input-group-append>
                        <b-button variant="success" @click="copyPressed">Copy</b-button>
                    </b-input-group-append>
                </b-input-group>
            </b-container>
            <b-list-group>
                <b-list-group-item v-for="(link, index) in links" :key="`link-${index}`">
                    <b-row align-h="between" style="padding-left:10px;padding-right:10px">
                        <b-button @click="(evt) => testButtonClicked(evt, link)" variant="dark" size="sm">{{link.title}}</b-button>
                        <b-button @click="(evt) => showModal(evt, link, index)" variant="warning" size="sm">Edit</b-button>
                        <b-button @click="(evt) => removeClicked(evt, index)" variant="danger" size="sm">Remove Item</b-button>
                    </b-row>
                </b-list-group-item>
            </b-list-group>
        </b-card>
        <b-modal 
            id="modal-prevent-closing" 
            ref="modal"
            title="Edit Entry"
            @show="resetModal"
            @hidden="resetModal"
            @ok="editconfirmed"
        >
            <b-form-input
                id="input-1"
                v-model="editText"
                required
                placeholder="Enter Title: (NOTE: 40 characters or less)"
                maxlength="40"
            ></b-form-input>
            <b-form-input
                id="input-2"
                v-model="editLink"
                required
                type="url"
                placeholder="Enter URL: (eg. http://www.google.com)"
            ></b-form-input>
        </b-modal>
    </b-container>
</template>
<script>
import {ultraSecretUberHiddenKey} from '../contants'
import CryptoJS from 'crypto-js'

export default {
    data: () => {
        return {
            linkText: '',
            linkLink: '',
            links: [],
            generated: '',
            editingIndex: -1,
            editText: '',
            editLink: ''
        }
    },
    methods: {
        onSubmit(evt) {
            evt.preventDefault()

            this.links.push({
                title: this.linkText,
                link: this.linkLink
            })
            this.linkText = '';
            this.linkLink = '';
        },
        removeClicked(evt, index) {
            evt.preventDefault()
            this.links.splice(index, 1);
        },
        generateLink() {
            let linksString = JSON.stringify(this.links)
            var ciphertext = CryptoJS.AES.encrypt(linksString, ultraSecretUberHiddenKey);
            this.generated = `${window.location.href.replace("#", '')}${ciphertext.toString()}`;
        },
        copyPressed() {
            if (this.generated.length <= 0) {
                alert("Nothing to copy");
                return;
            }
            var copyText = document.getElementById("generatedOuput");

            /* Select the text field */
            copyText.select(); 
            copyText.setSelectionRange(0, 99999); /*For mobile devices*/

            /* Copy the text inside the text field */
            document.execCommand("copy");

            alert("Copied to clipboard");
        },
        testButtonClicked(evt, link) {
            window.open(link.link, '_blank');
        },
        resetModal() {
            this.editingIndex = -1;
            this.editText = '';
            this.editLink = '';
        },
        editconfirmed() {
            if (this.links[this.editingIndex] != null) {
                this.links[this.editingIndex].title = this.editText;
                this.links[this.editingIndex].link = this.editLink;
            }
            this.resetModal();
        },
        showModal(evt, item, index) {
            this.$root.$emit('bv::show::modal', 'modal-prevent-closing', '#btnShow');
            this.editText = item.title;
            this.editLink = item.link;
            this.editingIndex = index;
        },
        showHelp() {
            alert("Link Generator is a tool designed to get around the ONE website link limit enforced by Instagram. Simply add as many URL's with titles as you want, and then click generate. A custom URL will be generated that can now be used in your Instagram bio to displays all your links when clicked.");
        }
    }
}
</script>